export const setDate = (date: string) => {
  const Time: string = dateFormat(date, "HH:MM");
  const Day: string = dateFormat(date, "DDDD");
  const FullDate: string = dateFormat(date, "d mmmm");

  return `${
    Day === "Yesterday" ? "" : Day === "Today" ? "" : FullDate
  }, ${Time} i-GMT+3`;
};
